:root {
  --bs-body-font-family: Roboto Serif, Inter, system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Noto Sans, Liberation Sans, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
  --bs-body-color: rgb(156, 163, 175);
  --bs-body-color-rgb: 156, 163, 175;
  --bs-border-color: rgba(229, 231, 235, 0.164);
  --bs-border-color-translucent: rgba(229, 231, 235, 0.164);
  --bs-link-color: rgb(255, 255, 255);
}

body {
  background: linear-gradient(to bottom, rgb(35, 45, 59), rgb(17, 24, 39));
  min-height: 100vh;
  background-attachment: fixed;
}

h1, h2, h3, h4, h5, h6 {
  color: rgb(255, 255, 255);
}

.card {
  background: linear-gradient(to bottom, rgb(35, 45, 59), rgb(17, 24, 39));
  --bs-card-cap-color: rgb(255, 255, 255);
}

input.form-control {
  background-color: rgb(17, 24, 39);
  color: rgb(255, 255, 255);
  border-color: rgba(229, 231, 235, 0.274);
}
input.form-control:focus {
  background-color: rgb(17, 24, 39);
  color: rgb(255, 255, 255);
  border-color: rgba(229, 231, 235, 0.274);
}

textarea.form-control {
  background-color: rgb(17, 24, 39);
  color: rgb(255, 255, 255);
  border-color: rgba(229, 231, 235, 0.274);
}
textarea.form-control:focus {
  background-color: rgb(17, 24, 39);
  color: rgb(255, 255, 255);
  border-color: rgba(229, 231, 235, 0.274);
}

.form-select {
  background-color: rgb(17, 24, 39);
  color: rgb(255, 255, 255);
  border-color: rgba(229, 231, 235, 0.274);
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
}

.alert-error {
  --bs-alert-color: #842029;
  --bs-alert-bg: #f8d7da;
  --bs-alert-border-color: #f5c2c7;
}

.list-group-item {
  color: rgb(156, 163, 175);
  background: linear-gradient(to bottom, rgb(35, 45, 59), rgb(17, 24, 39));
  border-color: rgba(229, 231, 235, 0.164);
}

.modal {
  --bs-modal-bg: rgb(35, 45, 59);
}

.toast {
  --bs-toast-bg: rgb(35, 45, 59);
  --bs-toast-header-bg: rgb(35, 45, 59);
  --bs-toast-header-color: rgb(255, 255, 255);
  --bs-toast-box-shadow: 0 0.5rem 1rem rgb(255 255 255 / 15%);
  --bs-toast-header-border-color: rgb(255 255 255 / 12%);
}

.btn-close {
  background: transparent url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath d='M.293.293a1 1 0 0 1 1.414 0L8 6.586 14.293.293a1 1 0 1 1 1.414 1.414L9.414 8l6.293 6.293a1 1 0 0 1-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 0 1-1.414-1.414L6.586 8 .293 1.707a1 1 0 0 1 0-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
}

.btn-custom-primary {
  --bs-btn-color: rgb(255, 255, 255);
  --bs-btn-bg: rgb(59, 130, 246);
  --bs-btn-border-color: transparent;
  --bs-btn-hover-color: #d9d9d9;
  --bs-btn-hover-bg: #0a59da;
  --bs-btn-hover-border-color: transparent;
  --bs-btn-focus-shadow-rgb: 49,132,253;
  --bs-btn-active-color: #bfbfbf;
  --bs-btn-active-bg: #073b91;
  --bs-btn-active-border-color: #84b1f9;
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #a6a6a6;
  --bs-btn-disabled-bg: #042761;
  --bs-btn-disabled-border-color: #042761;
}

.btn-custom-tertiary {
  background: linear-gradient(to bottom right, rgb(79, 70, 229), rgb(120, 51, 234));
  --bs-btn-color: rgb(255, 255, 255);
  --bs-btn-border-color: transparent;
  --bs-btn-hover-color: #d9d9d9;
  --bs-btn-hover-bg: #251bc3;
  --bs-btn-hover-border-color: transparent;
  --bs-btn-focus-shadow-rgb: 49,132,253;
  --bs-btn-active-color: #bfbfbf;
  --bs-btn-active-bg: #2118ad;
  --bs-btn-active-border-color: #8f89ee;
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #a6a6a6;
  --bs-btn-disabled-bg: #100c53;
  --bs-btn-disabled-border-color: #100c53;
}