@import "variables";

:root {
  --bs-body-font-family: #{$font-family};
  --bs-body-color: #{$text-color-3};
  --bs-body-color-rgb: #{red($text-color-3)}, #{green($text-color-3)}, #{blue($text-color-3)};
  --bs-border-color: #{$card-border-color-1};
  --bs-border-color-translucent: #{$card-border-color-1}; // Used for card border
  --bs-link-color: #{$text-color-1};
}

body {
  background: $background-primary;
  min-height: 100vh;
  background-attachment: fixed;
}

h1, h2, h3, h4, h5, h6 {
  color: $text-color-1;
}

.card {
  // --bs-card-bg: #{$card-bg-1};
  background: $background-primary;
  --bs-card-cap-color: #{$text-color-1};
}

@mixin common-form-styles-1 {
  background-color: $bg-dark-5;
  color: $text-color-1;
  border-color: $border-color-2;
}

input.form-control {
  @include common-form-styles-1;

  &:focus {
    @include common-form-styles-1;
  }
}

textarea.form-control {
  @include common-form-styles-1;

  &:focus {
    @include common-form-styles-1;
  }
}

.form-select {
  @include common-form-styles-1;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
}

.alert-error {
  --bs-alert-color: #842029;
  --bs-alert-bg: #f8d7da;
  --bs-alert-border-color: #f5c2c7;
}

.list-group-item {
  color: $text-color-3;
  background: $background-primary;
  border-color: $card-border-color-1;
}

.modal {
  --bs-modal-bg: #{$bg-dark-3};
}

.toast {
  --bs-toast-bg: #{$bg-dark-3};
  --bs-toast-header-bg: #{$bg-dark-3};
  --bs-toast-header-color: #{$text-color-1};
  --bs-toast-box-shadow: 0 0.5rem 1rem rgb(255 255 255 / 15%);
  --bs-toast-header-border-color: rgb(255 255 255 / 12%);
}

.btn-close {
  background: transparent url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath d='M.293.293a1 1 0 0 1 1.414 0L8 6.586 14.293.293a1 1 0 1 1 1.414 1.414L9.414 8l6.293 6.293a1 1 0 0 1-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 0 1-1.414-1.414L6.586 8 .293 1.707a1 1 0 0 1 0-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
}

.btn-custom-primary {
  --bs-btn-color: #{$text-color-1};
  --bs-btn-bg: #{$btn-bg-1};
  --bs-btn-border-color: #{$btn-border-color-1};
  --bs-btn-hover-color: #{$text-hover-color-1};
  --bs-btn-hover-bg: #{$btn-hover-bg-1};
  --bs-btn-hover-border-color: #{$btn-border-color-1};
  --bs-btn-focus-shadow-rgb: 49,132,253;
  --bs-btn-active-color: #{$text-active-color-1};
  --bs-btn-active-bg: #{$btn-active-bg-1};
  --bs-btn-active-border-color: #{$btn-border-color-active-1};
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #{$text-disabled-color-1};
  --bs-btn-disabled-bg: #{$btn-disabled-bg-1};
  --bs-btn-disabled-border-color: #{$btn-disabled-bg-1};
}

.btn-custom-tertiary {
  background: $btn-background-tertiary;
  --bs-btn-color: #{$text-color-1};
  --bs-btn-border-color: #{$btn-border-color-3};
  --bs-btn-hover-color: #{$text-hover-color-1};
  --bs-btn-hover-bg: #{$btn-hover-bg-3};
  --bs-btn-hover-border-color: #{$btn-border-color-3};
  --bs-btn-focus-shadow-rgb: 49,132,253;
  --bs-btn-active-color: #{$text-active-color-1};
  --bs-btn-active-bg: #{$btn-active-bg-3};
  --bs-btn-active-border-color: #{$btn-border-color-active-3};
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #{$text-disabled-color-1};
  --bs-btn-disabled-bg: #{$btn-disabled-bg-3};
  --bs-btn-disabled-border-color: #{$btn-disabled-bg-3};
}
